.insight{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: toRem(0);
  position: relative;
  padding-top: toRem(144);
  padding-bottom: toRem(88);
  justify-content: center;
  list-style-type: none;
  overflow: hidden;
  
  
  .insight--container{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: toRem(0);
    position: relative;
    justify-content: center;
    
    gap: toRem(56);
    .insight-text-tittle{
      width: toRem(899);
      font-weight: 800;
      text-align: center;
      letter-spacing: -0.01em;
      color: #0B0A0F;
      
      @include media-breakpoint-down(md){
        width: auto;
      }
  
      span{
        color: #0FBFE5;
      }
    }
  
  
    .insight-wrapper{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: toRem(30);
      
  
      @include media-breakpoint-down(xl){
        max-width: toRem(960);
      }
      @include media-breakpoint-down(lg-2){
        max-width: toRem(920);
      }
      @include media-breakpoint-down(lg){
        width: 90%;
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
        grid-gap: toRem(30);
        align-items: center;
        padding: 3rem toRem(20) 0rem;
      }
  
      .insight-card{
        padding: toRem(50) toRem(50) toRem(0) ;
        background-color: #F6F7FB;
        display: flex;
        flex-direction: column;
        
        
        @include media-breakpoint-down(xl){
          padding: toRem(50) toRem(20) toRem(0) ;
          max-width: toRem(680);
        }
        @include media-breakpoint-down(lg-2){
          max-width: toRem(500);
        }
  
        @include media-breakpoint-down(lg){
          display: flex;
          justify-content: center;
          flex-direction: column;
          align-items: center;
          text-align: center;
          max-width: max-content;
        }
        
        .card__tittle{
          width: toRem(466);
          font-weight: 700;
          color: #0B0A0F;
          @include media-breakpoint-down(xl){
            width: toRem(390);
          }
          @include media-breakpoint-down(lg){
            text-align: center;
          }
          @include media-breakpoint-down(md){
            width: auto;
          }
        }
        .card__list{
          text-align: start;
          width: 100%;
          padding: 0;
          li{
            list-style-type: none;
            width: toRem(466);
            font-weight: 400;
            font-size: toRem(18);
            line-height: toRem(28);
            color: #394150;
            margin: toRem(24) 0rem;
            display: flex;
  
            @include media-breakpoint-down(xl){
              width: toRem(390);
            }
            @include media-breakpoint-down(lg){
              text-align: start;
              align-items: center;
            }
            @include media-breakpoint-down(md){
              width: auto;
            }
            img{
              margin-top: 0rem !important;
              margin-right: toRem(16);
            }
          }
        }
        .card__img{
          margin-top: toRem(32);
          width: 100%;
        }
      }
    }
  
    .insight-btn{
      margin-top: toRem(24);
      display: flex;
      width: 100%;
      justify-content: center;
    }
  }
} 